html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
var,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
video,
time,
mark,
audio
{
    border: 0;
    font: inherit;
    font-size: 100%;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote {
    quotes: none;
    ::before,
    ::after{
        content: '';
        content: none;
    };
}
q {
    quotes: none;
    ::before,
    ::after{
        content: '';
        content: none;
    };
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}
a {
    text-decoration: none;
}
button:focus {
    outline: none;
}
